
    import { useState } from 'react';
    import photo from "./image/profile.jpg";
    import './App.css';
    
    function App() {
    
        const[skills, setSkills] = useState("My Name is Weini")
    
        const handleName =()=> {
             setSkills( " My Name is Weini")
        }
    
        const handleSkills =()=> {
            setSkills("I'm a Frontend Developer")
        }
    
        const [count, setCount] = useState(0)
    
        return(
         <>   
            <main className="profile-container">
                <div className="profile">
                    <img src={photo} alt="profile" className="photo"/>
                </div>
                <h1>Hello!</h1>
                <div className="function">
                    <div >{skills}</div>
                    <div className="btns">
                        <button onClick={handleName} class="Name">Name</button>
                        <button onClick={handleSkills} class="Skills">Skills</button>
                    </div>
                </div>
                <div className="likes">
                    <div>{count}</div>
                    <div>
                        <button onClick ={() => setCount(count + 1)}>
                            <i class="fas fa-heart"></i> 
                        </button>
                    </div>
                </div>
    
                <div className="social">
                     <a href="https://www.facebook.com/helen.goitom.581/" target="blank"> 
                        <i class="fab fa-facebook-f"></i> 
                    </a>
                </div>
            </main>
        </>
        )
    }
         export default App
    
        
    